.fancy-button {
    display: inline-block;
    padding: .75rem 1.25rem;
    border-radius: 10rem;
    color: #fff;
    text-transform: uppercase;
    font-size: 1rem;
    letter-spacing: .15rem;
    transition: all .3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
}

.fancy-button:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #f3b24f;
    border-radius: 10rem;
    z-index: -2;
}

.fancy-button:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: #da8a0f;
    transition: all .3s;
    border-radius: 10rem;
    z-index: -1;
}

.fancy-button:hover {
    color: #fff;
}

.fancy-button:hover:before {
    width: 100%;
}
.field input {
