    font-size: 1.5em;
    transition: padding 0.3s 0.2s ease;
    color: white
}

.field input:focus {
    padding-bottom: 5px;
}

.field input:focus+.line:after {
    transform: scaleX(1);
}

.field {
    position: relative;
    margin: 15px;
    text-align: left;
}

.field .line {
    width: 100%;
    height: 3px;
    position: absolute;
    bottom: -8px;
    background: #bdc3c7;
}

.field .line:after {
    content: " ";
    position: absolute;
    float: right;
    width: 100%;
    height: 3px;
    transform: scalex(0);
    transition: transform 0.3s ease;
    background: #f3b24f;
}
